.c-copy-code.c-copy-code__hide-button
{
    opacity:0;
}

pre.c-copy-code__preflash--step-1
{
    --height:40px;
    transition: all 2000ms ease-in-out;
    overflow-y: hidden;
    
    &:after 
    {
        transition: all 175ms ease-in-out;
        content:"Copied to clipboard";
        position: absolute;
        width:100%;
        bottom:calc(var(--height) * -1);
        height:var(--height);
        left:0;
        display: flex;
        justify-content: center;
        align-items: center;
        background:blanchedalmond;
        color: var(--color-accent-dark);

    }
}
pre.c-copy-code__preflash
{
    &:after 
    {
        bottom:0%;
    }

}