// Settings used with preprocessors and contain font, colors definitions, etc.
// In this layer is common define the variables that can customize the template.
// $colors: (
//    color-primary: #000,
//    color-accent: #E7AD99
// );

@import "./root";

@font-face{
   font-family: 'JetBrains Mono';
   src: url('./fonts/mono/eot/JetBrainsMono-Regular.eot') format('embedded-opentype'),
        url('./fonts/mono/woff2/JetBrainsMono-Regular.woff2') format('woff2'),
        url('./fonts/mono/woff/JetBrainsMono-Regular.woff') format('woff');
   font-weight: normal;
   font-style: normal;
}

